import React, { useState } from 'react';
import { withGoogleMap, withScriptjs, GoogleMap, Marker, InfoWindow } from 'react-google-maps';
import io from 'socket.io-client';
const socket = io('https://codidrive.com//location_driver', { path: '/codiapi/socket.io' });

const MapConfig = () => {
  const [windowInfo, setWindowInfo] = useState(null);
  const [markers, setMarkers] = useState([]);
  socket.on('position_web', (marker) => {
    const markerExists = markers.some((m) => m.unidad === marker.unidad);
    if (!markerExists) {
      setMarkers([...markers, marker]);
    } else {
      const updatedMarkers = markers.map((m) => {
        if (m.unidad === marker.unidad) {
          return {
            ...m,
            lat: marker.lat,
            lng: marker.lng,
          };
        }
        return m;
      });
      setMarkers(updatedMarkers);
    }
  });

  return (
    <GoogleMap
      defaultOptions={{
        styles: require(`./stylemap.json`),
      }}
      defaultZoom={13.8}
      defaultCenter={{ lat: -6.77137, lng: -79.84088 }}
    >
      {markers.map((marker) => (
        <Marker
          position={{ lat: parseFloat(marker.lat), lng: parseFloat(marker.lng) }}
          onClick={() => {
            setWindowInfo(marker);
          }}
          icon={{
            url: `data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2238%22%20height%3D%2238%22%20viewBox%3D%220%200%2038%2038%22%3E%3Cpath%20fill%3D%22%231E293B%22%20stroke%3D%22%23ccc%22%20stroke-width%3D%22.5%22%20d%3D%22M34.305%2016.234c0%208.83-15.148%2019.158-15.148%2019.158S3.507%2025.065%203.507%2016.1c0-8.505%206.894-14.304%2015.4-14.304%208.504%200%2015.398%205.933%2015.398%2014.438z%22%2F%3E%3Ctext%20transform%3D%22translate%2819%2018.5%29%22%20fill%3D%22%23fff%22%20style%3D%22font-family%3A%20Arial%2C%20sans-serif%3Bfont-weight%3Abold%3Btext-align%3Acenter%3B%22%20font-size%3D%2212%22%20text-anchor%3D%22middle%22%3E${marker.unidad}%3C%2Ftext%3E%3C%2Fsvg%3E`,
            scaledSize: { width: 60, height: 60 },
          }}
        />
      ))}
      {windowInfo ? (
        <InfoWindow
          options={{ pixelOffset: new window.google.maps.Size(0, -50) }}
          onCloseClick={() => {
            setWindowInfo(!windowInfo);
          }}
          position={{
            lat: parseFloat(windowInfo.lat),
            lng: parseFloat(windowInfo.lng),
          }}
        >
          <div
            style={{
              display: 'flex',
              width: '180px',
              justifyContent: 'center',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <h2
              style={{ fontSize: 22, textAlign: 'center', fontWeight: 'bold', color: 'black' }}
            >{`Unidad: ${windowInfo.unidad}`}</h2>

            <img
              alt="profil"
              src="https://res.cloudinary.com/frapoteam/image/upload/v1654392189/taxi-driver_vwivsj.png"
              style={{ width: '70px' }}
            />
            <h2
              style={{ fontSize: 18, marginTop: 6, fontWeight: 'bold', color: 'black' }}
            >{`${windowInfo.nombres}`}</h2>
          </div>
        </InfoWindow>
      ) : null}
    </GoogleMap>
  );
};

const MapWrapped = withScriptjs(withGoogleMap(MapConfig));

const Map = () => {
  return (
    <MapWrapped
      googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.REACT_APP_MAPKEY}`}
      loadingElement={<div style={{ height: `100%`, width: `100%`, margin: 0 }} />}
      containerElement={<div style={{ height: `100%`, width: `100%`, margin: 0 }} />}
      mapElement={<div style={{ height: `100vh`, width: `100%`, margin: 0 }} />}
    />
  );
};
export default Map;
